// *****************************************************
// *****************************************************
// moving the object
function move(i) {
  var element = document.getElementById(divJson[i].id);
  element.style.position = "absolute";
  // to make image dragable again

  element.onmousedown = function (e) {
    if (e.button === 0) {
      offset = [element.offsetLeft - e.clientX, element.offsetTop - e.clientY];
      dragValue = element;
      dragValue.style.zIndex = zIndex++;

      curtHeight = parseInt(dragValue.style.height, 10);
      curtWidth = parseInt(dragValue.style.width, 10);
      // to make image dragable again

      // adjust frame and elemnt show in foreground
      element.style.zIndex = zIndex++;
      frame.style.zIndex = topLine.style.zIndex = zIndex - 1;

      window.addEventListener("mousemove", moved);
      e.preventDefault(); // Prevent selection

      let mousePosition = {
        x: e.clientX,
        y: e.clientY
      };

      let distX = offset[0] + mousePosition.x;
      let distY = offset[1] + mousePosition.y;

      //     add frame points to the div
      topW.style.left = distX - 20 + "px";
      topW.style.top = distY - 20 + "px";
      topW.className = dragValue.id;

      topN.style.left = distX + curtWidth / 2 - 5 + "px";
      topN.style.top = distY - 20 + "px";
      topN.className = dragValue.id;

      topE.style.left = distX + curtWidth + 11 + "px";
      topE.style.top = distY - 20 + "px";
      topE.className = dragValue.id;

      botW.style.left = distX - 20 + "px";
      botW.style.top = distY + curtHeight + 10 + "px";
      botW.className = dragValue.id;

      botS.style.left = distX + curtWidth / 2 - 5 + "px";
      botS.style.top = distY + curtHeight + 10 + "px";
      botS.className = dragValue.id;

      botE.style.left = distX + curtWidth + 11 + "px";
      botE.style.top = distY + curtHeight + 10 + "px";
      botE.className = dragValue.id;

      midW.style.left = distX - 20 + "px";
      midW.style.top = distY + curtHeight / 2 + "px";
      midW.className = dragValue.id;

      midE.style.left = distX + curtWidth + 11 + "px";
      midE.style.top = distY + curtHeight / 2 - 5 + "px";
      midE.className = dragValue.id;

      topLine.style.left = distX + curtWidth / 2 - 1 + "px";
      topLine.style.top = distY - 40 + "px";

      topLineSquare.style.left = distX + curtWidth / 2 - 5 + "px";
      topLineSquare.style.top = distY - 44 + "px";

      //     add frame to the div
      frame.style.left = distX - 16 + "px";
      frame.style.top = distY - 16 + "px";
      frame.style.width = curtWidth + 30 + "px";
      frame.style.height = curtHeight + 30 + "px";
    }
  };

  function moved(e) {
    if (e.buttons === 0) {
      dragValue = null;
      window.removeEventListener("mousemove", moved);
    } else {
      let mousePosition = {
        x: e.clientX,
        y: e.clientY
      };

      let distX = offset[0] + mousePosition.x;
      let distY = offset[1] + mousePosition.y;

      // move the div
      dragValue.style.left = distX + "px";
      dragValue.style.top = distY + "px";

      //     add frame points to the div
      topW.style.left = distX - 20 + "px";
      topW.style.top = distY - 20 + "px";
      topW.className = dragValue.id;

      topN.style.left = distX + curtWidth / 2 - 5 + "px";
      topN.style.top = distY - 20 + "px";
      topN.className = dragValue.id;

      topE.style.left = distX + curtWidth + 11 + "px";
      topE.style.top = distY - 20 + "px";
      topE.className = dragValue.id;

      botW.style.left = distX - 20 + "px";
      botW.style.top = distY + curtHeight + 10 + "px";
      botW.className = dragValue.id;

      botS.style.left = distX + curtWidth / 2 - 5 + "px";
      botS.style.top = distY + curtHeight + 10 + "px";
      botS.className = dragValue.id;

      botE.style.left = distX + curtWidth + 11 + "px";
      botE.style.top = distY + curtHeight + 10 + "px";
      botE.className = dragValue.id;

      midW.style.left = distX - 20 + "px";
      midW.style.top = distY + curtHeight / 2 + "px";
      midW.className = dragValue.id;

      midE.style.left = distX + curtWidth + 11 + "px";
      midE.style.top = distY + curtHeight / 2 - 5 + "px";
      midE.className = dragValue.id;

      topLine.style.left = distX + curtWidth / 2 - 1 + "px";
      topLine.style.top = distY - 40 + "px";

      topLineSquare.style.left = distX + curtWidth / 2 - 5 + "px";
      topLineSquare.style.top = distY - 44 + "px";

      //     add frame to the div
      frame.style.left = distX - 16 + "px";
      frame.style.top = distY - 16 + "px";
      frame.style.width = curtWidth + 30 + "px";
      frame.style.height = curtHeight + 30 + "px";
    }
  }
}
